class Menu {
  constructor() {
    console.log('>>> Menu constructor');

    const menuToggle = $('.js-toggle-menu');
    const menu = $('.menu');

    this.openMenu(menuToggle, menu);
    this.closeOutside(menuToggle, menu);
  }

  openMenu(menuToggle, menu) {
    const that = this;

    menuToggle.on('click', function(e) {
      e.preventDefault();

      // Toggle overlay
      if (!menuToggle.hasClass('-active')) {
        that.toggleOverlay(false);
        that.toggleMenu(menuToggle, menu, false);
      } else {
        that.toggleOverlay(true);
        that.toggleMenu(menuToggle, menu, true);
      }
    });
  }

  closeMenu(menuToggle, menu) {
    const that = this;

    // Toggle overlay
    that.toggleOverlay(true);

    // Toggle menu
    that.toggleMenu(menuToggle, menu, true);
  }

  toggleOverlay(isVisible) {
    let body = $('body');

    if (isVisible) {
      body.removeClass('-overlay');
    } else {
      body.addClass('-overlay');
    }
  }

  toggleMenu(menuToggle, menu, isVisible) {
    if (isVisible) {
      menuToggle.removeClass('-active');
      menu.removeClass('-show');
    } else {
      menuToggle.addClass('-active');
      menu.addClass('-show');
    }
  }

  closeOutside(menuToggle, menu) {
    const that = this;

    // Escape key
    $(document).on('keydown', function(e) {
      if (e.keyCode === 27)
        that.closeMenu(menuToggle, menu);
    });

    // Escape click outside
    window.addEventListener('click', function(e){
      let container = $('.-overlay');
      if (container.is(e.target)) {
        that.closeMenu(menuToggle, menu);
      }
    });

    // Escape on resize
    $(window).resize(function() {
      if($(window).outerWidth() >= 992) {
        that.closeMenu(menuToggle, menu);
      }
    });
  }
}

export default Menu;
